<template>
<div class="cate">
	<div>
	<div class="search_input" >	
		<div class="flex-item">
			<input type="" name="" id="  " value="" placeholder="输入商品"/>
			<img src="../../assets/shops/icons/vip-qr.png"/>
		</div>
	</div>
	</div>
	<ul class="sidebar" style="height: 650px;">		
		<li  :class="key===index?'active-border':''" class="hit" v-for="(nav,index) in lists" @click="toggleStyle(nav.cate_name,index,nav)">{{nav.cate_name}}</li>	
	</ul>
	<div class="content">
			                                    
		<div class="cates">				
			<div class="tit">{{cate_name}}</div>			
			<div >
			<div class="col_3">
				<a href="#" v-for="item in subList" @click="goodsList">									
					<img :src="item.pic_url"/>
					<div class="goods-name">{{item.cate_name}}</div>
				</a>				
				<div class="clear"></div>
			</div>
			</div>
		</div>
	</div>
</div>
</template>
<script>
  import defen from './defen'
  import mixture from './mixture'
	export default {
		data() {
			return {
				lists:[
					
				],
				subList:[],
				key:0,
				cate_name:'热门类目',
				items:[
//					{src:'../../../static/imgs/logo.png',name:'美妆'},
//					{src:'../../../static/imgs/logo.png',name:'护肤'},
//					{src:'../../../static/imgs/logo.png',name:'美妆'},
//					{src:'../../../static/imgs/logo.png',name:'护肤'}
				],
		        flag: 0,
		        jifen:'',
		        carousel:[],
			}
		},
	    components: {
	      defen,
	      mixture
	    },
		methods:{
      enterUrl(url){
        if (url !== undefined && url !== '') {
          location.href = url;
        }
      },
      convert(){
        this.$router.push('/convert')
      },
		  clearNav(){
		    let list = Array.from(document.querySelectorAll('.nav_font'));
		    list.forEach(item => {
		      item.className = 'nav_font'
        })
      },
      
     toggleStyle(cate_name,key,nav){
     	
     	this.cate_name=cate_name
     	this.key=key
     	console.log(nav)
     	this.subList=nav.cateInfo
		this.subList.forEach(function(item){
			if(item.pic_url.indexOf('http')===-1){
				item.pic_url=this.imageURL(item.pic_url)
			}else{
				return 1
			}
    				
    				
    			}.bind(this))
     },
      
      goodsList(){
      	this.$router.push('/goodsList')
      },
      getCategory(){
      	this.post('home/Category/getCateInfo', {}).then(function(res) {
      		console.log(res)

		if(res.data.code===10000){
    			this.lists=res.data.data;    		
//  			this.subList=this.lists[0].cateInfo
//  			this.items.forEach(function(item){
//  				item.pic_url=this.imageURL(item.pic_url)
//  				
//  			}.bind(this))
    		}
        }.bind(this))
      }
		},
    mounted(){
     this.getCategory(1)
     
//	var SideBar=document.getElementsByClassName('sidebar')[0];
//	var oLi=SideBar.getElementsByTagName('li');
//		for(var i=0;i<oLi.length;i++){
//			oLi[i].index=i;
//			oLi[i].onclick=function(){
//				for(var j=0;j<oLi.length;j++){
//					oLi[j].className='';				
//				}
//				this.className='active-border';
//			}
//		}
    }
    
	}
</script>
<style type="text/css" scoped>
@import "../../../static/fenlei.css";
.clear{clear: both;}
  .banner{
    height: 2.4rem;
    background-color: #F2F2F2;
  }
	.img_box {
    width: 100%;
    padding-top: 2.4rem;
    position: relative;
    background-color: #F2F2F2;
  }
  .img_box>img {
    position: absolute;
    top: 0;
    width: 100%;
    max-height: 100%;
  }
  .tip {
    padding: 0.15rem;
    border-bottom: 1px solid #F2F2F2;
  }
  .tip>img {
    width: 0.3rem;
    vertical-align: center;
    margin-right: 0.2rem;
  }
  .tip>span {
    font-size: 0.24rem;
    color: #666666;
  }
  .integrate {
    padding: 0.2rem;
    display: flex;
  }
  .integrate>section {
    flex: 1;
    text-align: center;
  }
  .integrate>section span {
    font-size: 0.262rem;
    color: #333333;
  }
  .integrate>section span strong {
    font-weight: normal;
    color: #ff6f00;
  }
  .inter {
    height: 0.2rem;
    background-color: #F2F2F2;
  }
  /*//导航切换*/
  .integrate_nav {
    height: 0.88rem;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #F2F2F2;
  }
  .integrate_nav li {
    flex: 1;
    text-align: center;
  }
  .nav_font {
    min-width: 1rem;
    display: inline-block;
    height: 0.86rem;
    line-height: 0.86rem;
    font-size: 0.3rem;
    color: #111111;
  }
  .nav_active {
    color: #ff6f00;
    border-bottom: 2px solid #ff6f00;
  }
  /*nav切换动画*/
  .nav_trig {
    animation: nav 0.5s linear;
  }
  @keyframes nav {
    from{
      transform: translateX(100vw);
    }
    to{
      transform: translateX(0);
    }
  }
  
  /*分类*/
.cates{
	padding: 10px;
	padding-top: 0;
}
.col_3{background: #fff;height: auto;display: block;}
.fl_goods{padding: .2rem;box-sizing: border-box;background: #F8F8F8;}
.fl_goods img{width: 50px;height: 50px;display: block;margin: auto;}

.over{white-space: nowrap;overflow: hidden;text-overflow: ellipsis;}
.search_input input{border: 0;outline: none;background: #EEEEEE;border-radius: 6px;text-align: center;}
.tit{text-align: center;padding: .35rem;}
.tit:before,.tit:after {
    content: '';
    width: 0.2rem;
    height: 1px;
    background-color: #666666;
    display: inline-block;
    vertical-align: middle;
    margin: 0 10px;
  }
  .active-border:after{
	content: ' ';
	position: absolute;
	left: 0;
	width: 1px;
	color: #FF5773;
	height: 100%;
	transform: translateY(0.5);
}
.hit{
	position: relative;
}
</style>



