<template>
  <div class="defen">
    <ul class="integrate_goods" v-infinite-scroll="loadMore" infinite-scroll-disabled="moreLoading" infinite-scroll-distance="0" infinite-scroll-immediate-check="false">
      <li v-for="i in goodsList" @click="seeInfo(i)">
        <section class="good_img">
          <img :src="i.pic_url" alt="">
        </section>
        <p class="good_desc">
          {{i.good_name}}
        </p>
        <p style="color: #ff6f00;font-size: 0.32rem;margin-top: -.05rem">¥{{i.good_price}}</p>
        <a href="javascript:void(0)" class="good_btn"> {{i.jifen}}麦粒可兑换</a>
      </li>
    </ul>
    <div class="more_loading" v-show="!queryLoading && goodsList.length !== 0">
      <mt-spinner type="fading-circle"  :size="25" v-show="moreLoading&&!allLoaded"></mt-spinner>
      <span v-show="allLoaded">已全部加载</span>
    </div>
    <div v-if="goodsList.length === 0" class="no_goods">
      <img src="../../assets/no_goods.png" alt="" ><br>
      <span>暂无商品</span>
    </div>
  </div>
</template>
<script>
  export default {
    data (){
      return {
        goodsList: [],
        moreLoading: false,
        queryLoading: false,
        allLoaded: false,
        pageSize: 20,
        pageNum: 1,
        totalNum: 0,
      }
    },
    props:['money'],
    methods: {
      loadMore(){
        if(this.allLoaded){
          this.moreLoading = true;
          return;
        }
        if(this.queryLoading){
          return;
        }
        this.moreLoading = !this.queryLoading;
        this.pageNum++;
        this.getDefenGoods(this.pageNum);
      },
      seeInfo(item){
        let url = `/mixtureInfo/${item.id}/${this.money}`;
        this.$router.push(url);
      },
      //获取积分商品
      getDefenGoods(page = 1){
        this.post('home/Shop/getAllShopList', {
          page: page,
          number: 10,
          type: 3
        }).then(res => {
          if (res.data.code === 10000) {
            this.totalNum = res.data.info.number;
            this.goodsList = this.goodsList.concat(res.data.info.data);
            this.allLoaded = this.goodsList.length === this.totalNum;
            this.moreLoading = this.allLoaded;
            this.goodsList.forEach(item => {
              if (item.pic_url.indexOf('http') !== -1) {

              } else {
                item.pic_url = this.imageURL(item.pic_url);
              }
            })
          }
        })
      }
    },
    mounted(){
      this.getDefenGoods();
    }
  }
</script>
<style scoped>
  @import "../../../static/defen.css";
  .more_loading {
    display: flex;
    justify-content: center;
    padding: 0.2rem;
  }
</style>



